<template>
  <el-container>
    <div class="area-administrator-container" style="width: 100%;">
      <div class="table-header-btn">
        <div class='table-header-btn-l'>

        </div>
        <div class='table-header-btn-r'>
          <!-- <el-button type="primary" @click="editArea">编辑</el-button>
          <el-button type="danger">删除</el-button> -->
          <el-button type="primary" @click="initializeAres()">区域初始化</el-button>
          <el-button type="primary" size="small" @click="addArea">区域登记</el-button>
          <!-- <el-button type="primary" @click="sortArea">排序</el-button>
          <el-button type="primary" size="small" @click="changeStatusManagementArch(false)">禁用</el-button>
          <el-button type="primary" size="small" @click="changeStatusManagementArch(true)">启用</el-button>
          <el-button type="primary">权限数据初始化</el-button> -->
        </div>
      </div>
      <el-table :data="dataList" ref="areaListForm" row-key="areaId" :height="siteContentViewHeight - 56+'px'" highlight-current-row @current-change="currentChange">
        <el-table-column type="index" label="序号" align="center" width="50"></el-table-column>
        <el-table-column label="区域代码" prop="areaCode"></el-table-column>
        <el-table-column label="区域名称" prop="areaName"></el-table-column>
        <el-table-column label="区域级别" prop="areaLevel"></el-table-column>
        <el-table-column label="区域状态">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.areaStatus == 0" size="small" type="danger">停用</el-tag>
            <el-tag v-else-if="scope.row.areaStatus == -1" size="small" type="danger">未初始化</el-tag>
            <el-tag v-else size="small">启用</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="区域类型" prop="areaType"></el-table-column>
        <el-table-column label="操作" width="300">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" plain @click="editArea(scope.$index,scope.row)">修改</el-button>
            <el-button type="danger" size="mini" plain @click="deleArea(scope.$index,scope.row)">删除</el-button>
            <el-button type="primary" size="mini" plain @click="allotArea(scope.$index,scope.row)">菜单分配</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 添加、修改弹框 -->
    <el-dialog :title="title" width="60%" :visible.sync="addAreaVisiable" v-if='addAreaVisiable' :close-on-click-modal="false" :modal-append-to-body='false' append-to-body>
      <addareadialog @addback="addback" :checkedData="checkedData" :isEdit="isEdit"></addareadialog>
    </el-dialog>

    <!-- 菜单分配弹框 -->
    <el-dialog :title="title" width="50%" :visible.sync="allotAreaVisiable" v-if='allotAreaVisiable' :close-on-click-modal="false" :modal-append-to-body='false' append-to-body>
      <allotareadialog @addback="addback" :checkedData="checkedData"></allotareadialog>
    </el-dialog>

    <!-- 排序弹框 -->
    <el-dialog title="排序" width="50%" :visible.sync="sortAreaVisiable" v-if='sortAreaVisiable' :close-on-click-modal="false" :modal-append-to-body='false' append-to-body>
      <sortareadialog></sortareadialog>
    </el-dialog>
  </el-container>
</template>

<script>
import store from '@/store'
import addareadialog from './add-area-dialog.vue'
import sortareadialog from './sort-area-dialog.vue'
import allotareadialog from './allot-area-dialog.vue'
export default {
  name: 'area-administrator-index',//区域节点管理
  props: ['siteContentViewHeight'],
  components: {
    addareadialog,
    sortareadialog,
    allotareadialog,
  },
  data () {
    return {
      dataList: [],
      checkedData: {},//编辑、删除选中数据
      currentRow: "",//选中行数据
      addAreaVisiable: false,//添加、修改弹框
      sortAreaVisiable: false,//排序弹框
      allotAreaVisiable: false,//菜单分配弹框
      isEdit: '', //true：添加，false：编辑
    }
  },
  created () {
    this.getDataList();
    //菜单分配、需默认取到第一个系统id
    var data = JSON.parse(sessionStorage.getItem('navSystem') || '[]');
    this.$store.commit("common/updateAreaSysId", data[0].sysId);
  },
  methods: {
    //获取数据列表
    getDataList () {
      this.$http({
        url: this.$http.adornUrl(this.API.getAreaTreeArea),
        method: 'post',
        data: this.$http.adornData({

        })
      }).then((res) => {
        var resData = res.data;
        if (resData.code === 0) {
          this.dataList = resData.data;
        } else {
          this.$message.error(resData.msg);
        }
      })
    },

    //添加
    addArea () {
      this.title = "区域登记";
      this.isEdit = true;
      this.addAreaVisiable = true;
    },
    //编辑
    editArea (index, row) {
      this.checkedData = row;
      this.title = "编辑";
      this.isEdit = false;
      this.addAreaVisiable = true;
    },
    //分配菜单
    allotArea (index, row) {
      this.checkedData = row;
      this.title = "菜单分配";
      this.allotAreaVisiable = true;
    },
    //删除
    deleArea (index, row) {
      this.$confirm('确定删除选中记录?', '操作提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http({
          url: this.$http.adornUrl(this.API.deleteArea),
          method: 'post',
          data: this.$http.adornData({
            areaId: row.areaId
          })
        }).then(({ data }) => {
          if (data.code === 0) {
            this.getDataList();
            this.$message.success('删除成功');
          } else {
            this.$message.error(data.msg);
          }
        })
      }).catch(() => {

      });
    },

    //初始化区域
    initializeAres () {
      if (this.currentRow === null || this.currentRow === "") {
        this.$confirm('请选择一条数据！', '提示', {
          confirmButtonText: '确定',
          showCancelButton: false,
          type: 'warning',
          center: true
        });
        return;
      }
      this.$confirm('区域初始化后无法删除,是否继续初始化?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let infomessage = this.$message({
          message: '正在初始化 , 请稍后......',
          duration: 0
        });
        this.$http({
          url: this.$http.adornUrl(this.API.createRelationArea),
          method: 'post',
          data: this.$http.adornData({
            areaId: this.currentRow.areaId,
            areaCode: this.currentRow.areaCode,
            areaName: this.currentRow.areaName
          })
        }).then(({ data }) => {
          if (data.code === 0) {
            this.getDataList();
            infomessage.close()
            this.$message.success('初始化成功');
          } else {
            infomessage.close()
            this.$message.error(data.msg);
          }
        }).catch(err => {
          infomessage.close()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消区域初始化'
        });
      });
      // console.log(this.currentRow)
    },

    // 状态切换
    changeStatusManagementArch (status) {
      if (this.currentRow === null || this.currentRow === "") {
        this.$confirm('请选择一条数据！', '提示', {
          confirmButtonText: '确定',
          showCancelButton: false,
          type: 'warning',
          center: true
        });
        return;
      }
    },
    // 添加或修改组件回调方法--子组件回调数据的方法
    // @param {Boolean} isRefresh是否需要刷新数据 true=刷新；false=不刷新
    addback (isRefresh) {
      this.addAreaVisiable = false;
      this.allotAreaVisiable = false;
      if (isRefresh) {
        this.getDataList();
        store.dispatch('common/getNewMenuList')
      }
    },
    //排序
    sortArea () {
      this.sortAreaVisiable = true;
    },
    //  选中单元格事件
    currentChange (currentRow) {
      this.currentRow = currentRow;
    },
  }
}
</script>

<style scoped lang="scss">
</style>



