<template>
  <div>
    <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
      <el-tab-pane v-for="(item, index) in filtersDataList" :key="index" :label="item.sysName" :name="item.sysId"></el-tab-pane>
    </el-tabs>
    <div class="float-right">
      <el-checkbox v-model="checkedAll" @change="handleCheck">关联子节点</el-checkbox>
      <el-button type="primary" size="small" @click="getCurrentKey">保存</el-button>
    </div>
    <div class="clearfix"></div>
    <el-scrollbar style='height: 400px;' v-loading="treeLoading">
      <org-tree ref="tree" :treeData="treeData" :checkbox="treeCheckBox" :checkStrictly="checkStrictly" :defaultChecked="defaultChecked" @check="check"></org-tree>
    </el-scrollbar>
  </div>
</template>

<script>
import orgTree from "@/components/baseComponents/orgTree.vue";
export default {
  name: 'allot-area-dialog',
  props: [
    'checkedData'
  ],
  components: {
    orgTree
  },
  computed: {
    filtersDataList () {
      return this.dataList.filter(item => item.sysStsatus === '1')
    }
  },
  data () {
    return {
      roleId: '',
      checkedAll: false,
      checkStrictly: false,
      treeData: [],
      treeCheckBox: true,//tree复选框是否有
      defaultChecked: [],
      dataList: [],//机构数据
      sysId: '',//机构id
      activeName: this.$store.state.common.areaSysId,
      treeIds: [], // 所有树ID 用于统计当前树节点个数
      treeLoading: true
    }
  },
  watch: {},
  created () {
    this.sysDataList();
    var id = this.$store.state.common.areaSysId;
    this.getData(id);
  },
  methods: {
    // 当前点击的对象
    handleCheck (bol) {
      this.checkedAll = bol
      if (bol) {
        this.$refs.tree.setCheckedNodes(this.treeData);
      } else {
        this.$refs.tree.setCheckedNodes([]);
      }
    },
    // 获取当前点击的checkbox的对象
    check (currentObj, treeStatus) {
      this.restAllChecked()
      // // 用于：父子节点严格互不关联时，父节点勾选变化时通知子节点同步变化，实现单向关联。
      let selected = treeStatus.checkedKeys.indexOf(currentObj.id); // -1未选中
      // // 选中
      if (selected !== -1) {
        // 子节点只要被选中父节点就被选中
        this.selectedParent(currentObj)
        // 统一处理子节点为相同的勾选状态
        this.uniteChildSame(currentObj, true)
      } else {
        // 未选中 处理子节点全部未选中
        if (currentObj.children.length !== 0) {
          this.uniteChildSame(currentObj, false)
        }
      }
    },
    sysDataList () {
      this.$http({
        url: this.$http.adornUrl(this.API.getAllApplySystemInfoSystem),
        method: 'post',
        data: this.$http.adornData({
          searchCount: false, //分页默认true，不需要传false
        })
      }).then((res) => {
        var resData = res.data;
        if (resData.code === 0) {
          this.dataList = resData.data;
        } else {
          this.$message.error(resData.msg);
          this.dataList = [];
        }
      })
    },
    handleClick (tab) {
      this.sysId = tab.name;
      this.getData(this.sysId)
    },
    getData (id) {
      this.treeIds = [];
      this.$http({
        url: this.$http.adornUrl(this.API.getMenuTreetBysIdMenu),
        method: 'post',
        data: this.$http.adornData({
          sysId: id,
          areaId: this.checkedData.areaId,
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.treeData = data.data;
          this.defaultChecked = this.getIsCheckCurrent(data.data);
          this.treeLoading = false;
        } else {
          this.$message.error(data.msg);
          this.treeData = [];
          this.defaultChecked = [];
          this.treeLoading = false;
        }
      });
    },
    getCurrentKey () { //保存选中节点
      // console.log(this.$refs.tree.getCheckedKeys().concat(this.$refs.tree.getHalfCheckedKeys()).join(','))
      this.$http({
        url: this.$http.adornUrl(this.API.batchInsertSysAreaMenuArea),
        method: 'post',
        data: this.$http.adornData({
          areaId: this.checkedData.areaId, //区域id
          sysId: this.sysId || this.$store.state.common.areaSysId, //系统id
          menuId: this.$refs.tree.getCheckedKeys().join(','),//菜单id
        })
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message.success('操作成功');
          this.$emit("addback", true);
        } else {
          this.$message.error(data.msg);
        }
      });
    },
    getIsCheckCurrent (arr) { //多层树结构中取出是否选中
      const setCheckedKeys = [];
      if (!arr) return;
      const getChild = (arr) => {
        arr.forEach(item => {
          this.treeIds.push(item.id)
          if (item.isCheck) setCheckedKeys.push(item.id);
          if (item['children'] && item['children'].length > 0) {
            getChild(item.children)
          }
        })
        this.checkedAll = (setCheckedKeys.length === this.treeIds.length && this.treeIds.length > 0 ? true : false)
        return setCheckedKeys;
      }
      return getChild(arr)
    },
    // 统一处理子节点为相同的勾选状态
    uniteChildSame (treeList, isSelected) {
      this.$refs.tree.setChecked(treeList.id, isSelected)
      treeList.children.forEach((item, index) => {
        this.uniteChildSame(treeList.children[index], isSelected)
      })
    },
    // 统一处理父节点为选中
    selectedParent (currentObj) {
      let currentNode = this.$refs.tree.getNode(currentObj)
      console.log(currentNode)
      if (currentNode.parent.key !== undefined) {
        this.$refs.tree.setChecked(currentNode.parent, true)
        this.selectedParent(currentNode.parent)
      }
    },
    // 重置全选状态
    restAllChecked () {
      const crruntCheckedIdsLen = this.$refs.tree.getCheckedKeys().length;
      console.log(crruntCheckedIdsLen)
      this.checkedAll = (crruntCheckedIdsLen === this.treeIds.length ? true : false)
    }
  }
}
</script>

<style lang="scss" scoped>
.float-right {
  padding-bottom: 10px;
  margin-top: 10px;
  .el-checkbox {
    padding-right: 20px;
  }
}
</style>



